.projects {
  width: 100%;
  position: relative;
  background-color: #032B2F;
  padding-top: 40px;
  padding-bottom: 40px;
  color: #fff;

  h1 {
    font-size: 1.6rem;
    text-align: center;
    font-weight: 700;
    margin-bottom: 40px;
  }

  .projects_wrapper {
    width: 100%;
    max-width: 980px;
    margin: 0 auto;
  }

  section {
    border-radius: 6px;
    background-image: url("../images/site1.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 95%;
    height: 200px;
    margin: 15px auto;
    position: relative;
    z-index: 0;
    overflow: hidden;
    padding-left: 10px;

    h2 {
      font-size: 1.6rem;
      margin-top: 40px;
    }
    p {
      font-size: 0.8rem;
      margin-top: 20px;
      width: 45%;
    }
    a {
      color: #fff;
      width: 150px;
      height: 70px;
      display: block;
      background-color: #D73A31;
      border-radius: 6px;
      position: absolute;
      right: 10px;
      bottom: 10px;
      transition: 0.2s;

      h3 {
        font-size: 1.8rem;
        text-align: center;
        line-height: 70px;
      }
    }
    a:hover {
      background-color: #4C6677;
      cursor: pointer;
    }
  }
  section::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(1, 1, 1, 0.6);
    z-index: -1;
  }
}

@media screen and (min-width: 640px) {
  .projects_wrapper {
    section {
      height: 300px;

      h2 {
        font-size: 2.3rem;
      }
      p {
        font-size: 1.2rem;
      }
      a {
        width: 215px;
        height: 86px;
        h3 {
          line-height: 86px;
        }
      }
    }
  }
}
